.hotelContainer{
    display: flex;
    /* justify-content: center; */
    margin-top: 0px;
    flex-direction: column;
    align-items: center;
}
.hotelWrapper{
    width: 100%;
    max-width: 1024px;
    display: flex;
    flex-direction: column;
    gap: 10px;
    position: relative;
}
.bookNow{
    position: absolute;
    top: 10px;
    right: 0;
    border: none;
    padding: 10px 20px;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
    margin-top: 15px;
}
.hotelTitle{
    font-size: 24px;
    margin-top: 20px;
}
.hotelAddress{
    font-size: 12px;
    display: flex;
    align-items: center;
    gap: 10px;
}
.hotelDistance{
    color: #0071c2;
    font-weight: 500;
}
.hotelPriceHighlight{
    color: #008009;
    font-weight: 500;
}
.hotelImages{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.hotelImgWrapper{
    width: 33%;
}
.hotelImg{
    width: 100%;
    object-fit: cover;
    cursor: pointer;
}

.hotelDetails{
    display: flex;
    justify-content:space-between;
    gap: 20px;
    margin-top: 20px;
}
.hotelDetailsText{
    flex: 3;
}
.hotelDesc{
    font-size: 14px;
    margin-top: 20px;
}
.hotelDetailsPrice{
    flex: 1;
    background-color: #ebf3ff;
    padding: 20px;
    display: flex;
    flex-direction: column;
}
.hotelDetailsPrice>h1{
    font-size: 18px;
    color: #555;
}
.hotelDetailsPrice>span{
    font-size: 14px;
}
.hotelDetailsPrice>h2{
    font-weight: 300;
}
.hotelDetailsPrice>button{
    border: none;
    padding: 10px 20px;
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    cursor: pointer;
    border-radius: 5px;
}
.slider{
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    /* width: 50%; */
    height: 100vh;
    background-color:rgba(0, 0, 0, 0.471);
    z-index: 333;
    display: flex;
    align-items: center;
}
.sliderWrapper{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
}
.sliderImg{
    width: 80%;
    height: 80vh;
    margin:auto;
}
.close{
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 30px;
    color: lightgray;
    cursor: pointer;
}
.arrow{
    margin: 20px;
    font-size: 50px;
    cursor: pointer;
    color: lightgray;
}