.fp{
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.fpItem{
    flex: 1;
    gap: 10px;
    display: flex;
    flex-direction: column;
}
.fpImg{
    width: 100%;
}
.fpName{
    font-weight: bold;
}
.fpCity{
    font-weight: 400;
}
.fpPrice{
    font-weight: 500;
}
.fpRating>button{
cursor: pointer;
background-color: #003580;
color: white;
border: none;
padding: 3px;
margin-right: 10px;
font-weight: bold;
}
.fpRating>span{
    font-size: 14px;
}
@media (min-width:300px) and (max-width:500px) {
      
    .fp{   
       
        flex-direction: column;
        width: 90vh;
        margin-left: 180%;
        height: 200vh;
    }
    .fpName{
        font-size: 60px;
    }
    .fpCity{
        font-size: 50px;
    }
    .fpPrice{
        font-size: 40px;
    }
    .fpRating button{
        font-size: 40px ;
    }
    .fpRating span{
        font-size: 40px ;
    }
}